body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

header{top:0;z-index:1;width:100%;height:85px;position:fixed;font-size:24px;display:flex !important;background-color:#fff;justify-content:space-between;box-shadow:0 2px 7px 0 rgba(0,0,0,0.15)}header .logo-img{width:60px;height:60px;margin:12px 15px}header .navbar-toggler{border:unset;font-size:24px;padding:30px 15px}a{color:unset !important;text-decoration:none !important}.navbar{top:0;z-index:3;width:250px;height:100%;overflow:auto;font-weight:bold;display:block !important;padding:unset !important;position:fixed !important;background-color:#fff !important;box-shadow:0 0 10px rgba(0,0,0,0.1)}.navbar .btn-close{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.navbar .navbar-top{display:flex;padding:12px 15px;justify-content:space-between}.navbar .navbar-top .logo-img{width:60px;height:60px}.navbar .navbar-bottom{padding:12px 15px}.navbar .navbar-bottom a{padding:10px 0}.navbar .navbar-bottom .nav-item{display:block;text-align:left}.overlay{top:0;left:0;right:0;bottom:0;z-index:2;width:100%;height:100%;position:fixed;background-color:rgba(0,0,0,0.5)}

main{padding-left:250px}@media screen and (max-width: 767px){main{margin-top:85px;padding-left:unset}}main .banner{width:100%;height:500px}@media screen and (max-width: 767px){main .banner{height:300px}}

